import coopAbiJson from "../abi/coop.json";
import { utils } from "ethers";
import { Contract } from "@ethersproject/contracts";
import { useMemo } from "react";
import { useContractFunction } from "@usedapp/core";
import { COOP_ADDRESS_MAP, TARGET_CHAIN_ID } from '../constants';


export function useCoop() {
    const coopAddress = COOP_ADDRESS_MAP[TARGET_CHAIN_ID];
    const coopAbi = useMemo(() => new utils.Interface(coopAbiJson), []);
    const coopContract = useMemo(
        () => new Contract(coopAddress, coopAbi),
        [coopAddress, coopAbi]
    );

    const { state: stakeInCoopState, send: stakeInCoopSend } = useContractFunction(
        coopContract,
        "addManyToCoopAndPack",
        {
            transactionName: "Stake Haawk",
        }
    );

    const stakeInCoop = (userAddress, tokenIds) => {
        return stakeInCoopSend(userAddress, tokenIds);
    };

    const { state: claimEggState, send: claimEggSend } = useContractFunction(
        coopContract,
        "claimManyFromCoopAndPack",
        {
            transactionName: "Claim Egg",
        }
    );

    const claimEgg = (tokenIds) => {
        return claimEggSend(tokenIds, false);
    }

    const claimEggAndUnstake = (tokenIds) => {
        return claimEggSend(tokenIds, true);
    }
    
    return { stakeInCoop, stakeInCoopState, claimEgg, claimEggAndUnstake};
}
