import React, { useCallback, useState } from "react";
import { Helmet } from "react-helmet";
import { useTranslation } from "react-i18next";
import { useHaawk } from "./hooks/useHaawk";
import { useEgg } from "./hooks/useEgg";
import { useCoop } from "./hooks/useCoop";
import "./App.css";
import { useEthers, shortenIfAddress, useNotifications } from "@usedapp/core";
import { formatUnits } from "@ethersproject/units";
import ConnectButton from "./compenonts/ConnectButton";
import { TARGET_CHAIN_ID } from "./constants";
import { Link } from 'react-router-dom';

export default function App() {
    const { t, i18n } = useTranslation();

    const { account, chainId } = useEthers();
    // ÈìæÊ£ÄÊü•
    const isRightChain = chainId === TARGET_CHAIN_ID;

    const [darkMode, setDarkMode] = useState(false);
    const toggleDarkMode = () => {
        setDarkMode(!darkMode);
    };

    const {
        minted,
        mintHaawk,
        mintState,
        useUserHaawkIds,
        useUserUnstakeChickenIds,
        useUserUnstakeHawkIds,
        useUserCoopChickenIds,
        useUserPackHawkIds,
    } = useHaawk();
    const { stakeInCoop, claimEgg, claimEggAndUnstake } = useCoop();
    // ÊéßÂà∂ËøõÂ∫¶Êù°
    const mintedPercent = (parseFloat(minted) / 10000) * 100.0;

    const [mintAmount, setMintAmount] = useState(1);
    const mintAmountChange = useCallback((e) => {
        let val = e.target.value;
        if (parseInt(val) > 10) {
            val = 10;
        }
        if (parseInt(val) < 1) {
            val = 1;
        }
        setMintAmount(val);
    }, []);

    // Â§ÑÁêÜ mint Âä®‰Ωú
    const [isMintButtonEnable, setMintButtonEnable] = useState(true);
    const handleMint = useCallback(
        (mintWithStake) => {
            setMintButtonEnable(false);
            mintHaawk(mintAmount, mintWithStake)
                .then(() => {
                    setMintButtonEnable(true);
                })
                .catch((e) => {
                    console.log(e);
                    setMintButtonEnable(true);
                });
        },
        [mintAmount, mintHaawk]
    );

    // Ëé∑ÂèñÈí±ÂåÖÂÜÖ egg ‰ΩôÈ¢ù
    const { useEggBalance } = useEgg();
    const eggBalance = useEggBalance(account);
    // Ëé∑Âèñ Êú™Ë¥®Êäº chicken ÂàóË°®
    const userUnstakeChickenIds = useUserUnstakeChickenIds(account);
    // Ëé∑Âèñ Êú™Ë¥®Êäº hawk ÂàóË°®
    const userUnstakeHawkIds = useUserUnstakeHawkIds(account);

    // Ëé∑Âèñ Â∑≤Ë¥®Êäº chicken ÂàóË°®
    const userCoopChickenIds = useUserCoopChickenIds(account);
    // Ëé∑Âèñ Â∑≤Ë¥®Êäº hawk ÂàóË°®
    const userCoopHawkIds = useUserPackHawkIds(account);

    // ÊéßÂà∂ Can Stake ÈÄâ‰∏≠ÂàóË°®
    const [checkedCanStakeItems, setCheckedCanStakeItems] = useState({});
    const handleCanStakeSelect = (e) => {
        setCheckedCanStakeItems({
            ...checkedCanStakeItems,
            [e.target.name]: e.target.checked,
        });
    };

    // Can Stake ÈÄâ‰∏≠ÁöÑ ids
    let canStakeSelectedList = [];
    Object.keys(checkedCanStakeItems).forEach((key) => {
        if (checkedCanStakeItems[key]) {
            canStakeSelectedList.push(key.replace("cst-", ""));
        }
    });

    // Â§ÑÁêÜ Stake Âä®‰Ωú
    const [isStakeButtonEnable, setStakeButtonEnable] = useState(true);
    const handleStake = useCallback(() => {
        let canStakeSelectedList = [];
        Object.keys(checkedCanStakeItems).forEach((key) => {
            if (checkedCanStakeItems[key]) {
                canStakeSelectedList.push(key.replace("cst-", ""));
            }
        });

        setStakeButtonEnable(false);
        stakeInCoop(account, canStakeSelectedList)
            .then(() => {
                setStakeButtonEnable(true);
            })
            .catch((e) => {
                console.log(e);
                setStakeButtonEnable(true);
            });
    }, [account, checkedCanStakeItems, stakeInCoop]);

    // ÊéßÂà∂ Coop Staked ÈÄâ‰∏≠ÂàóË°®
    const [checkedCoopStakeItems, setCheckedCoopStakeItems] = useState({});
    const handleCoopStakeSelect = (e) => {
        setCheckedCoopStakeItems({
            ...checkedCoopStakeItems,
            [e.target.name]: e.target.checked,
        });
    };

    // Coop Stake ÈÄâ‰∏≠ÁöÑ ids
    let coopStakeSelectedList = [];
    Object.keys(checkedCoopStakeItems).forEach((key) => {
        if (checkedCoopStakeItems[key]) {
            coopStakeSelectedList.push(key.replace("coop-", ""));
        }
    });

    // Â§ÑÁêÜ Claim Âä®‰Ωú
    const [isClaimButtonEnable, setClaimButtonEnable] = useState(true);
    const handleClaim = useCallback(() => {
        let coopStakeSelectedList = [];
        Object.keys(checkedCoopStakeItems).forEach((key) => {
            if (checkedCoopStakeItems[key]) {
                coopStakeSelectedList.push(key.replace("coop-", ""));
            }
        });

        setClaimButtonEnable(false);
        claimEgg(coopStakeSelectedList)
            .then(() => {
                setClaimButtonEnable(true);
            })
            .catch((e) => {
                console.log(e);
                setClaimButtonEnable(true);
            });
    }, [checkedCoopStakeItems, claimEgg]);

    // Â§ÑÁêÜ Claim & Unstake Âä®‰Ωú
    const handleClaimAndUnstake = useCallback(() => {
        let coopStakeSelectedList = [];
        Object.keys(checkedCoopStakeItems).forEach((key) => {
            if (checkedCoopStakeItems[key]) {
                coopStakeSelectedList.push(key.replace("coop-", ""));
            }
        });

        setClaimButtonEnable(false);

        claimEggAndUnstake(coopStakeSelectedList)
            .then(() => {
                setClaimButtonEnable(true);
            })
            .catch((e) => {
                console.log(e);
                setClaimButtonEnable(true);
            });
    }, [checkedCoopStakeItems, claimEggAndUnstake]);

    // const { notifications } = useNotifications()
    // console.log(notifications);

    return (
        <>
            <Helmet>
                <title>ü¶Ö {t("Hawk Game")}</title>
                <html class={darkMode ? "paper dark" : "paper light"} />
            </Helmet>
            <div className="row flex-center">
                <div className="col-10 col">
                    <center>
                        <h1>ü¶Ö {t("Hawk Game")}</h1>
                        {isRightChain ? (
                            <></>
                        ) : (
                            <>
                                <div className="alert alert-danger">
                                    {t("Wrong Network")} ChainId = {chainId}
                                </div>
                            </>
                        )}
                        <p>
                            <button
                                className="btn-small"
                                onClick={() =>
                                    i18n.changeLanguage(
                                        i18n.language === "zh" ? "en" : "zh"
                                    )
                                }
                            >
                                üåè {t("Switch Lang")}
                            </button>
                            <button
                                className="btn-small margin-left"
                                onClick={toggleDarkMode}
                            >
                                {darkMode ? t("Light Mode") : t("Dark Mode")}
                            </button>
                            {account ? (
                                <>
                                    <button className="btn-small margin-left">
                                        {t("Address")}
                                        <span className="badge">
                                            {shortenIfAddress(account)}
                                        </span>
                                    </button>
                                </>
                            ) : (
                                <span className="margin-left">
                                    <ConnectButton sm />
                                </span>
                            )}
                        </p>
                    </center>
                    <div className="row">
                        <div className="lg-12 sm-12 md-12 col">
                            <div className="paper container-md margin-left margin-right">
                                <center>
                                    <p>
                                        {t("Description1")}
                                        <span className="text-secondary">
                                            {t("Description2")}
                                        </span>
                                        {t("Description3")}
                                    </p>
                                    <div>
                                        <span className="margin-small">
                                            <Link to="/whitepaper"
                                                class="paper-btn"
                                            >
                                                {t("Whitepaper")}
                                            </Link>
                                        </span>
                                        <span className="margin-small">
                                            <button
                                                onClick={() => alert("Soooon")}
                                            >
                                                {t("Discord")}
                                            </button>
                                        </span>
                                        <span className="margin-small">
                                            <a
                                                href="https://pancakeswap.finance/swap?outputCurrency=0x8f0528ce5ef7b51152a59745befdd91d97091d2f"
                                                target="_blank"
                                                rel="noreferrer"
                                                class="paper-btn"
                                            >
                                                {t("Buy EGG")}
                                            </a>
                                        </span>
                                        <span className="margin-small">
                                            <button
                                                onClick={() => alert("Soooon")}
                                            >
                                                {t("Twitter")}
                                            </button>
                                        </span>
                                    </div>
                                    <div className="margin">
                                        <Link to="/tos">{t("TOS")}</Link>
                                    </div>
                                </center>
                            </div>
                        </div>
                    </div>
                    <div className="row flex-center">
                        <div className="sm-12 md-12 lg-6 col">
                            <div className="paper container-md margin">
                                <h3>{t("Minting")}</h3>
                                <p>
                                    {minted} / {10000} GEN 0 {t("Minted")}
                                </p>
                                <div className="progress margin-bottom">
                                    <div
                                        className={`bar striped secondary w-${Math.round(
                                            mintedPercent
                                        )}`}
                                    >
                                        <span
                                            className="margin-left"
                                            style={{ whiteSpace: "nowrap" }}
                                        >
                                            {mintedPercent.toFixed(2)} %
                                        </span>
                                    </div>
                                </div>
                                <p>
                                    {t("Amount")}
                                    <input
                                        type="number"
                                        value={mintAmount}
                                        onChange={mintAmountChange}
                                        placeholder=""
                                        id="mint-amount"
                                    />
                                </p>
                                <p>
                                    {t("Cost")}
                                    {mintAmount * 0.01} BNB
                                </p>
                                <div>
                                    {account ? (
                                        <>
                                            <button
                                                onClick={() =>
                                                    handleMint(false)
                                                }
                                                disabled={!isMintButtonEnable}
                                            >
                                                {t("Mint")}
                                            </button>
                                            <button
                                                className="margin-left"
                                                onClick={() => handleMint(true)}
                                                disabled={!isMintButtonEnable}
                                            >
                                                {t("Mint and Stake")}
                                            </button>
                                        </>
                                    ) : (
                                        <ConnectButton />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="sm-12 md-12 lg-6 col">
                            <div className="paper container-md margin">
                                <p>
                                    {t("Wallet Tips")}
                                    <span className="text-secondary">
                                        {eggBalance
                                            ? parseFloat(
                                                  formatUnits(eggBalance, 18)
                                              ).toFixed(4)
                                            : "-"}
                                    </span>{" "}
                                    $EGG
                                </p>
                                <h3>{t("Unstaked")}</h3>
                                <ul>
                                    <li>{t("Can Stake")}</li>
                                    <ul>
                                        {userUnstakeHawkIds.length > 0 ? (
                                            userUnstakeHawkIds.map((v) => {
                                                const tokenId = v.toNumber();
                                                return (
                                                    <div
                                                        key={tokenId}
                                                        style={{
                                                            margin: "0 !important",
                                                        }}
                                                    >
                                                        <span
                                                            className="form-group"
                                                            style={{
                                                                marginBottom:
                                                                    "0 !important",
                                                            }}
                                                        >
                                                            <label
                                                                htmlFor={`cst-${tokenId}`}
                                                                className="paper-check"
                                                            >
                                                                <input
                                                                    type="checkbox"
                                                                    name={`cst-${tokenId}`}
                                                                    id={`cst-${tokenId}`}
                                                                    checked={
                                                                        checkedCanStakeItems[
                                                                            `cst-${tokenId}`
                                                                        ] ||
                                                                        false
                                                                    }
                                                                    onChange={
                                                                        handleCanStakeSelect
                                                                    }
                                                                />
                                                                <span>
                                                                    # {tokenId}{" "}
                                                                    ü¶Ö
                                                                </span>
                                                            </label>
                                                        </span>
                                                    </div>
                                                );
                                            })
                                        ) : (
                                            <></>
                                        )}
                                    </ul>
                                    <ul>
                                        {userUnstakeChickenIds.length > 0 ? (
                                            userUnstakeChickenIds.map((v) => {
                                                const tokenId = v.toNumber();
                                                return (
                                                    <div
                                                        key={tokenId}
                                                        style={{
                                                            margin: "0 !important",
                                                        }}
                                                    >
                                                        <span
                                                            className="form-group"
                                                            style={{
                                                                marginBottom:
                                                                    "0 !important",
                                                            }}
                                                        >
                                                            <label
                                                                htmlFor={`cst-${tokenId}`}
                                                                className="paper-check"
                                                            >
                                                                <input
                                                                    type="checkbox"
                                                                    name={`cst-${tokenId}`}
                                                                    id={`cst-${tokenId}`}
                                                                    checked={
                                                                        checkedCanStakeItems[
                                                                            `cst-${tokenId}`
                                                                        ] ||
                                                                        false
                                                                    }
                                                                    onChange={
                                                                        handleCanStakeSelect
                                                                    }
                                                                />
                                                                <span>
                                                                    # {tokenId}{" "}
                                                                    üê§
                                                                </span>
                                                            </label>
                                                        </span>
                                                    </div>
                                                );
                                            })
                                        ) : (
                                            <></>
                                        )}
                                    </ul>
                                    <ul>
                                        {userUnstakeChickenIds.length < 1 &&
                                        userUnstakeHawkIds.length < 1 ? (
                                            <div className="badge">
                                                {t("No Tokens")}
                                            </div>
                                        ) : (
                                            <></>
                                        )}
                                    </ul>
                                </ul>
                                {canStakeSelectedList &&
                                canStakeSelectedList.length > 0 ? (
                                    <div>
                                        <p>
                                            {t("Selected")}
                                            {canStakeSelectedList.map((v) => (
                                                <span key={`css-key-${v}`}>
                                                    #{v}{" "}
                                                </span>
                                            ))}
                                        </p>
                                        <button
                                            disabled={!isStakeButtonEnable}
                                            onClick={() => handleStake()}
                                        >
                                            {t("Stake Selected")}
                                        </button>
                                    </div>
                                ) : (
                                    <></>
                                )}
                                <h3>{t("Staked")}</h3>
                                <ul>
                                    <li>{t("Coop")}</li>

                                    <ul>
                                        {userCoopChickenIds &&
                                        userCoopChickenIds.length > 0 ? (
                                            userCoopChickenIds.map((v) => {
                                                const tokenId = v.toNumber();
                                                return (
                                                    <div
                                                        key={tokenId}
                                                        style={{
                                                            margin: "0 !important",
                                                        }}
                                                    >
                                                        <span
                                                            className="form-group"
                                                            style={{
                                                                marginBottom:
                                                                    "0 !important",
                                                            }}
                                                        >
                                                            <label
                                                                htmlFor={`coop-${tokenId}`}
                                                                className="paper-check"
                                                            >
                                                                <input
                                                                    type="checkbox"
                                                                    name={`coop-${tokenId}`}
                                                                    id={`coop-${tokenId}`}
                                                                    checked={
                                                                        checkedCoopStakeItems[
                                                                            `coop-${tokenId}`
                                                                        ] ||
                                                                        false
                                                                    }
                                                                    onChange={
                                                                        handleCoopStakeSelect
                                                                    }
                                                                />
                                                                <span>
                                                                    # {tokenId}{" "}
                                                                    üê§
                                                                </span>
                                                            </label>
                                                        </span>
                                                    </div>
                                                );
                                            })
                                        ) : (
                                            <div className="badge">
                                                {t("No Tokens")}
                                            </div>
                                        )}
                                    </ul>
                                </ul>

                                <ul>
                                    <li>{t("Hawk Pack")}</li>
                                    <ul>
                                        {userCoopHawkIds &&
                                        userCoopHawkIds.length > 0 ? (
                                            userCoopHawkIds.map((v) => {
                                                const tokenId = v.toNumber();
                                                return (
                                                    <div
                                                        key={tokenId}
                                                        style={{
                                                            margin: "0 !important",
                                                        }}
                                                    >
                                                        <span
                                                            className="form-group"
                                                            style={{
                                                                marginBottom:
                                                                    "0 !important",
                                                            }}
                                                        >
                                                            <label
                                                                htmlFor={`coop-${tokenId}`}
                                                                className="paper-check"
                                                            >
                                                                <input
                                                                    type="checkbox"
                                                                    name={`coop-${tokenId}`}
                                                                    id={`coop-${tokenId}`}
                                                                    checked={
                                                                        checkedCoopStakeItems[
                                                                            `coop-${tokenId}`
                                                                        ] ||
                                                                        false
                                                                    }
                                                                    onChange={
                                                                        handleCoopStakeSelect
                                                                    }
                                                                />
                                                                <span>
                                                                    # {tokenId}{" "}
                                                                    ü¶Ö
                                                                </span>
                                                            </label>
                                                        </span>
                                                    </div>
                                                );
                                            })
                                        ) : (
                                            <div className="badge">
                                                {t("No Tokens")}
                                            </div>
                                        )}
                                    </ul>
                                </ul>

                                {coopStakeSelectedList &&
                                coopStakeSelectedList.length > 0 ? (
                                    <div>
                                        <p>
                                            {t("Selected")}
                                            {coopStakeSelectedList.map((v) => (
                                                <span key={`css-key-${v}`}>
                                                    #{v}{" "}
                                                </span>
                                            ))}
                                        </p>
                                        <button
                                            disabled={!isClaimButtonEnable}
                                            onClick={() => handleClaim()}
                                        >
                                            {t("Claim")}
                                        </button>
                                        <button
                                            disabled={!isClaimButtonEnable}
                                            onClick={() =>
                                                handleClaimAndUnstake()
                                            }
                                            className="margin-left"
                                        >
                                            {t("Claim and Unstake")}
                                        </button>
                                    </div>
                                ) : (
                                    <></>
                                )}
                                <p className="text-muted">{t("Select Tips")}</p>
                            </div>
                        </div>
                    </div>
                    <div className="row flex-center">
                        <div className="col">
                            <p className="text-muted">{t("Footer")}</p>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
}
