import eggAbiJson from "../abi/egg.json";
import { utils } from "ethers";
import { Contract } from "@ethersproject/contracts";
import { useMemo } from "react";
import { useTokenBalance } from "@usedapp/core";
import { EGG_ADDRESS_MAP, TARGET_CHAIN_ID } from '../constants'; 

export function useEgg() {
    const eggAddress = EGG_ADDRESS_MAP[TARGET_CHAIN_ID];
    const eggAbi = useMemo(() => new utils.Interface(eggAbiJson), []);
    const eggContract = useMemo(
        () => new Contract(eggAddress, eggAbi),
        [eggAddress, eggAbi]
    );

    const useEggBalance = (address) => useTokenBalance(eggAddress, address)
    
    return { useEggBalance };
}
