import haawkAbiJson from "../abi/haawk.json";
import dataAbiJson from "../abi/haawk_data.json";
import { utils } from "ethers";
import { Contract } from "@ethersproject/contracts";
import { useMemo } from "react";
import { useContractCall, useContractFunction } from "@usedapp/core";
import { COOP_ADDRESS_MAP, HAAWK_ADDRESS_MAP, HAAWK_DATA_ADDRESS_MAP, TARGET_CHAIN_ID } from '../constants';


export function useHaawk() {
    const haawkAddress = HAAWK_ADDRESS_MAP[TARGET_CHAIN_ID];
    const haawkAbi = useMemo(() => new utils.Interface(haawkAbiJson), []);
    const haawkContract = useMemo(
        () => new Contract(haawkAddress, haawkAbi),
        [haawkAddress, haawkAbi]
    );

    const dataAddress = HAAWK_DATA_ADDRESS_MAP[TARGET_CHAIN_ID];
    const dataAbi = useMemo(() => new utils.Interface(dataAbiJson), []);

    const coopAddress = COOP_ADDRESS_MAP[TARGET_CHAIN_ID];

    const [minted] = useContractCall({
        abi: haawkAbi,
        address: haawkAddress,
        method: "minted",
        args: [],
    }) ?? ["-"];

    const { state: mintState, send: mintSend } = useContractFunction(
        haawkContract,
        "mint",
        {
            transactionName: "Mint Haawk",
        }
    );

    const mintHaawk = (mintAmount, isStake) => {
        return mintSend(mintAmount, isStake, {
            value: utils.parseUnits(String(0.01 * mintAmount), "ether"),
        });
    };

    const useUserHaawkIds = (userAddress) => {
        const [userHaawkIds] =
            useContractCall(haawkAddress &&
                userAddress && {
                abi: dataAbi,
                address: dataAddress,
                method: "getUserHaawkIds",
                args: [haawkAddress, userAddress],
            }) ?? [[]];
        return userHaawkIds;
    };

    const useUserUnstakeChickenIds = (userAddress) => {
        const [userUnstakeChickenIds] =
            useContractCall(haawkAddress &&
                userAddress && {
                abi: dataAbi,
                address: dataAddress,
                method: "getUserUnstakeChickenIds",
                args: [haawkAddress, userAddress],
            }) ?? [[]];
        return userUnstakeChickenIds.filter(v => v.toNumber() > 0);
    };

    const useUserUnstakeHawkIds = (userAddress) => {
        const [userUnstakeHawkIds] =
            useContractCall(haawkAddress &&
                userAddress && {
                abi: dataAbi,
                address: dataAddress,
                method: "getUserUnstakeHawkIds",
                args: [haawkAddress, userAddress],
            }) ?? [[]];
        return userUnstakeHawkIds.filter(v => v.toNumber() > 0);
    };

    const useUserCoopChickenIds = (userAddress) => {
        const [userCoopHaawkIds] =
            useContractCall(coopAddress &&
                userAddress && {
                abi: dataAbi,
                address: dataAddress,
                method: "getUserCoopChickenIds",
                args: [coopAddress, userAddress],
            }) ?? [[]];
        return userCoopHaawkIds.filter(v => v.toNumber() > 0);
    }

    const useUserPackHawkIds = (userAddress) => {
        const [userPackHawkIds] =
            useContractCall(coopAddress &&
                userAddress && {
                abi: dataAbi,
                address: dataAddress,
                method: "getUserPackHawkIds",
                args: [coopAddress, userAddress],
            }) ?? [[]];
        return userPackHawkIds.filter(v => v.toNumber() > 0);
    }

    return { minted, mintHaawk, mintState, useUserHaawkIds, useUserUnstakeChickenIds, useUserUnstakeHawkIds, useUserCoopChickenIds, useUserPackHawkIds };
}
